import { useState } from "react";

import { Link } from "react-router-dom";
import logo from "../images/logo.png";
import "./Navbar.css";
import { BiMenuAltRight, BiMenuAltLeft } from "react-icons/bi";

const Navbar = ({ children }) => {
	const [menuOpen, setMenuOpen] = useState(false);
	const openMenu = () => {
		setMenuOpen(!menuOpen);
	};
	return (
		<div>
			<nav className="nav">
				<div className="navContainer">
					<img src={logo} className="logo" alt="logo" />

					<ul className="ul">
						<li className="li">
							<Link className="a" to="/">
								Home
							</Link>
						</li>
						<li className="li">
							<Link className="a" to="/work">
								My Work
							</Link>
						</li>
						<li className="li">
							<Link className="a" to="/aboutme">
								About Me
							</Link>
						</li>
					</ul>

					{(!menuOpen && (
						<BiMenuAltLeft
							className="menu"
							onClick={() => openMenu()}
						/>
					)) ||
						(<BiMenuAltRight className="menu menuClose" onClick={() => openMenu()} />)}
				</div>
			</nav>

			<div className={menuOpen ? "navMenu open" : "navMenu"}>
				<ul className="menuUl">
					<li className="menuLi">
						<a className="menuA"  href="/">
							Home
						</a>
					</li>
					<li className="menuLi">
						<a className="menuA" href="/work">
							My Work
						</a>
					</li>
					<li className="menuLi">
						<a className="menuA" href="/aboutme">
							About Me
						</a>
					</li>
				</ul>
				<BiMenuAltRight className="menu menuClose" onClick={() => openMenu()} />
			</div>
		</div>
	);
};

export default Navbar;
